import React from 'react';
import PropTypes from "prop-types";
import "./movie.css";
const Movie = ({ year ,title, summary, poster, genres }) => {
    return (
        <div className="movie">
            <img src={poster} alt={title} title={title}/>
           <div classnam="movie__data">
                 <h3 classnam="movie__title">{title}</h3>
                <h5 classnam="movie__year">{year}</h5>
                <ul classnam="movie__genres">
                    {genres.map((genre , item) => (
                         <li key={item } className="genres__genre">
                            {genre}
                        </li>
                    ))}
                </ul>    
                <p classnam="movie__summary">{summary.slice(0,140)} ... </p>            
           </div>
        </div>
    );
};


Movie.prototype={
    id : PropTypes.number.isRequired,
    year : PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary : PropTypes.string.isRequired,
    poster : PropTypes.string.isRequired,
    genres : PropTypes.arrayOf(PropTypes.string).isRequired
}
export default Movie;